.button {
  border: none;
  color: white;
  background-color: #4a026b;
  font-size: 1.5rem;
  font-weight: 400;
  width: 120px;
  padding: 5px 5px 5px 5px;
  border-radius: 3px;
  margin-top: 10px;
}

.button:hover {
  background-color: #600a87;
}

.button:active {
  background-color: #520b73;
}
